import { useState } from "react";
import pass from "../../Assets/images/password.svg";
import login from "../../Assets/images/Mask Group 2.png";
import "./ChangePasswod.css";
import Axios from "axios";
import { useSelector } from "react-redux";

const ChangePassword = () => {
  const [old_password, setOld_Password] = useState("");
  const [new_password, setNew_Password] = useState("");

  const items = localStorage.getItem("user");
  const obj = JSON.parse(items);
  const user_id = obj._id;
  const user_type = obj.account_type;

  const BASE_API_URL = useSelector((state) => state.BASE_API_URL);

  const handleSubmit = (e) => {
    e.preventDefault();
    Axios.put(
      `${BASE_API_URL}/api/users/update-user-password/${user_id}?old_password=${old_password}&new_password=${new_password}&user_type=${user_type}`
    )
    .then((res)=>{
        console.log(res.data)
    })
    .catch((err)=>{
        console.log(err)
    })
  };

  return (
    <div className="changepassword">
      <div className="form">
        <div className="header">
          <h2>Change Password</h2>
          <img src={pass} alt="" />
        </div>
        <form onSubmit = {handleSubmit}>
          <div>
            <label>Old Password</label>
            <input type="password" placeholder="old password" defaultValue={old_password} onChange={(e)=> setOld_Password(e.target.value)} />
          </div>
          <div>
            <label>New Password</label>
            <input type="password" defaultValue={new_password} onChange={(e)=> setNew_Password(e.target.value)} />
          </div>
          <div>
            <label>Confirm New Password</label>
            <input type="password" />
          </div>
          <button type="submit">Submit</button>
        </form>
      </div>
      <div className="section2">
        <img src={login} alt="" />
      </div>
      <div></div>
    </div>
  );
};

export default ChangePassword;
